.container {
    margin: auto auto;
    height: 100%;
    padding: 0 10px 0 10px;
}

.picture_container {
    margin-bottom: 15px;
}

@media (min-width: 940px) {
    .container {
        padding: 0 20px 0 20px;    
        display: -ms-flexbox;    
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
    }
    
    .picture_container {
        width: 47.5%;
        padding-left: 2.5%;
        float: right;
        height: 100%;
        margin-bottom: 0;
    }

    .data_container {
        -ms-flex: 1 1;
            flex: 1 1;
    }

    .chart_container {
        width: 50%;
        height: 100%;
    }
}



