.underTitleContainer {
    display: block;
    margin-bottom: 10vh;
    text-align: center;
}

.underTitle {
    color: white;
    font-size: 15px;
    font-family: 'Unica One', cursive;
    font-weight: lighter;
}

.underTitleSpacer {
    position: relative; 
    bottom: 5px;
    margin: 0 5px 0 5px;
}

.githubLinkContainer {
    display: inline-block;
}

@media (min-width: 600px) {
    .underTitle {
        font-size: 1.5em;
    }
}


