/***** Fonts *****/
@import url('https://fonts.googleapis.com/css?family=Unica+One|Vollkorn');

body, html, #root {
    height: 100%;
}

.highlight {
    color: #08FEB8;
}

.highlight:hover {
    -webkit-text-decoration-color: #08FEB8;
            text-decoration-color: #08FEB8;
}

a {
    text-decoration: none;
    color: inherit;
}

/***** Base styles *****/
html {
    background-color: #222;
}

body {
    margin: 0;
}



