.title {
    color: white;
    font-size: 30px;
    font-family: 'Unica One', sans-serif;
    font-weight: bold;
    letter-spacing: .1em;
    text-align: center;
    margin: 0 0 10px 0;
    padding-top: 10vh;
}

@media (min-width: 600px) {
    .title {
        font-size: 4em;
    }
}


